import React, {Component} from 'react'
import axios from 'axios'
import {Link} from 'react-router'

import InstitutionListCardComponent from './InstitutionListCardComponent'
import InstitutionListingComponent from './InstitutionsListingComponent'

export default class InstitutionListContainer extends Component{
    constructor(props){
        super(props);
        this.state = {
            institution:''
        }
      }

    componentWillMount = () =>{
       this.getAllInstitution();
    }

    getAllInstitution = () =>{
        axios.get('http://localhost:8080/api/institution')
        .then((response)=>{
            this.setState({
                institution:response.data.map(this.composeInstitution)
            })
            console.log(response.status)
        })
        .catch((erorr) => {
            console.log(erorr)
        })
    }
    removeHandler = (inst_id) =>{
        axios.delete('http://localhost:8080/api/institution/'+inst_id)
        .then((response)=>{
            console.log(response.status)
            this.getAllInstitution()
        })
        .catch((erorr) => {
            console.log(erorr)
        })
    }

    composeInstitution = (inst, index) =>{
        return(
        <InstitutionListCardComponent
            key={index}
            id={inst.id}
            title={inst.title}
            city={inst.city}
            image={inst.image}
            category={inst.category}
            libraryBookStoreType={inst.libraryBookStoreType}
            bookRentalType={inst.bookRentalType}
            bookArchiveType={inst.bookArchiveType}
            removeHandler={this.removeHandler}
        />)
    }

      render(){
          return(<div>
              <Link to={'/institution/add/new'} className='btn btn-primary'>Create new</Link>
              <InstitutionListingComponent  institution={this.state.institution}/></div>)
      }
}


