import React, {Component} from 'react'
import axios from 'axios'
import {Link} from 'react-router'

import BookListCardComponent from './BookListCardComponent'
import BookTableListComponent from './BookTableListComponent'

export default class BookListContainer extends Component{
    constructor(props){
        super(props);
        this.state = {
            books:''
        }
      }
    componentWillMount = () =>{
        this.getBooksFromInstitution()
    }

    getBooksFromInstitution = () =>{
    axios.get('http://localhost:8080/api/book')
    .then((response)=>{
        this.setState({
            books:response.data.map(this.composeBooks)
        })
        console.log(response.status)
    })
    .catch((erorr) => {
        console.log(erorr)
    })
    }

    deleteHandler = (book_id) =>{
        axios.delete('http://localhost:8080/api/book/'+book_id)
        .then((response)=>{
            console.log(response.status)
            this.getBooksFromInstitution();
        })
        .catch((erorr) => {
            console.log(erorr)
        })
        

    }
        
    composeBooks = (book,index) =>{
        return (
            <BookListCardComponent 
            id={book.id}
            title={book.title}
            author={book.author}
            pages={book.pages}
            image={book.image}
            price={book.price}
            condition={book.condition}
            quantity={book.quantity}
            deleteHandler={this.deleteHandler}
            />
        )
    }

    
    render(){
        return(<div>
            <Link to={'/books/create'} className='btn btn-primary'>New Book</Link>

            <BookTableListComponent deleteHandler={this.deleteHandler} books={this.state.books}/></div>)
    }
}

