import React, {Component} from 'react'
import {Link} from 'react-router'



const InstitutionListCardComponent = (props) =>{
    return(
            <tr key={props.key}>
                <td>{props.image}</td>
                <td>{props.title}</td>
                <td>{props.city}</td>
                <td>{props.category}</td>
                <td>{props.libraryBookStoreType}</td>
                <td>{props.bookRentalType}</td>
                <td>{props.bookArchiveType}</td>
                <td><Link to={'/institution/'+props.id+'/edit'} className='btn btn-primary'>Edit</Link></td>
                <td><Link to={'/institution/'+props.id} className='btn btn-primary'>View</Link></td>
                <td><button onClick={() => props.removeHandler(props.id)} className='btn btn-danger'>Remove</button></td>
            </tr>
    )
}

export default InstitutionListCardComponent


