import React, {Component} from 'react'

import axios from 'axios'

import InstitutionForm  from './InstitutionForm'

export default class InstitutionEditContainer extends Component{

    constructor(props){
        super(props);
        this.inst_id = this.props.params.inst_id
        this.state = {
            title:'',
            city:'',
            image:'',
            category:'',
            libraryBookStoreType:'ADULT',
            bookRentalType:'OLD_BOOKS',
            bookArchiveType:'PRIVATE',

            institutionType:'BOOK_STORE'
        }
      }
      fieldHandler = (e) =>{
        this.setState({[e.target.name]: e.target.value})
        console.log("Input field name: " + e.target.name)
        console.log("Input field value: " + e.target.value)
      }

      submitHandler = (e) =>{
        e.preventDefault();
        axios.put('http://localhost:8080/api/institution/'+this.inst_id, {
            institutionType:this.state.institutionType,
            title:this.state.title,
            city:this.state.city,
            image:this.state.image,
            category:this.state.category,
            libraryBookStoreType:this.state.libraryBookStoreType,
            bookRentalType:this.state.bookRentalType,
            bookArchiveType:this.bookArchiveType
        })
        .then((response)=>{
            console.log(response.status)
            this.setState({
                number:'',
            })
        })
        .catch((erorr) => {
            console.log(erorr)
        })
        }




      render(){
          return(<div><InstitutionForm onSubmit={this.submitHandler} fieldHandler={this.fieldHandler} /></div>)
      }
}


