import React, {Component} from 'react'


const BooksTableListComponent = (props) =>{
    return(
    <table class="table table-hover">
        <thead>
            <tr>
                <th>image</th>
                <th>title</th>
                <th>author</th>
                <th>price</th>
                <th>pages</th>
                <th>condition</th>
                <th>quantity</th>
                <th>OTHER</th>
            </tr>
        </thead>
        <tbody>
                {props.books}
        </tbody>
    </table>)
}

export default BooksTableListComponent


