import React, {Component} from 'react'


const InstitutionListingComponent = (props) =>{
    return (
        <table className="table table-hover"> 
        <thead>
            <tr>
                <th>image</th>
                <th>title</th>
                <th>city</th>
                <th>category</th>
                <th>Library or Store Type</th>
                <th>Rental type</th>
                <th>Archive Type</th>
            </tr>
        </thead>
        <tbody>{props.institution}</tbody>
        </table>
        
    )
}

export default InstitutionListingComponent


