import React, {Component} from 'react'
import {Link} from 'react-router'



const SingleInstitutionCardComponent = (props) =>{
    return(
            <tr key={props.key}>
                <td>Image:{props.image}</td>
                <td>Title:{props.title}</td>
                <td>City:{props.city}</td>
                <td>{props.category}</td>
                <td>{props.libraryBookStoreType}</td>
                <td>{props.bookRentalType}</td>
                <td>{props.bookArchiveType}</td>
            </tr>
    )
}

export default SingleInstitutionCardComponent


